<template>
  <div class="role">
      <div class="tree-content">
        <el-tabs v-model="treeType" class="tabs" @tab-click="handleClick">
        <el-tab-pane label="页面权限" name="页面"></el-tab-pane>
        <el-tab-pane label="接口权限" name="接口"></el-tab-pane>
      </el-tabs>
        <el-button type="primary" class="addPage-btn baseColor" icon="el-icon-plus" @click="addPage(0)">添加父级</el-button>
      <el-tree
        v-if="pageData&&pageData.length>0"
        ref="tree"
        :data="pageData"
        :props="defaultProps"
        class="tree1"
        :expand-on-click-node="false"
        :indent="30"
        :allow-drop="allowDrop"
      >
        <span class="custom-tree-node" slot-scope="{data}">
          <span class="name">
            <!-- <i v-if="data.ParentID==0" :class="el-icon-circle-plus-outline"></i> -->
            <img v-if="data.ParentID==0&&data.Icon!=''" :src="require('../../assets/images/'+data.Icon+'.png')" class="icon-titlle" alt="">
            <span >{{data.Label}}</span>
          </span>
          <span v-if="treeType == '页面'">
            <el-button type="text"  size="mini" v-if="data.ParentID==0" @click.stop="addPage(1,data)">添加子级</el-button>
            <el-button style="margin: 0 30px;" type="text" size="mini" @click.stop="addPage(2,data)">编辑</el-button>
            <el-button type="text" size="mini" @click.stop="delPage(data.ID)" slot="reference" style="color:#D27171;">删除</el-button>
          </span>
          <span v-else>
            <el-button type="text" v-if="data.Type=='页面'&&data.ParentID!==0" size="mini" @click.stop="addPage(1,data)">添加子级</el-button>
            <el-button type="text" v-if="data.Type=='接口'" size="mini" @click.stop="addPage(2,data)">编辑</el-button>
            <el-button type="text" v-if="data.Type=='接口'" size="mini" @click.stop="delPage(data.ID)" slot="reference" style="color:#D27171;">删除</el-button>
          </span>
        </span>
      </el-tree>
      </div>
    <el-dialog :visible.sync="showEdit" @close="resetVerForm" width="600px">
      <el-form
        :model="addForm"
        status-icon
        :rules="rules"
        ref="addForm"
        label-width="70px"
        class="demo-ruleForm"
      >
        <el-form-item label="名称" prop="Label">
          <el-input v-model="addForm.Label"></el-input>
        </el-form-item>
        <el-form-item label="Name" prop="Name" v-if="treeType == '页面'">
          <el-input v-model="addForm.Name"></el-input>
        </el-form-item>
        <el-form-item :required="typeAdd==true" label="路径" prop="Path">
          <el-input v-model="addForm.Path"></el-input>
        </el-form-item>
        <el-form-item v-if="treeType == '页面'" :required="typeAdd==false" label="icon" prop="Icon" >
          <el-input v-model="addForm.Icon"></el-input>
        </el-form-item>
        <el-form-item label="排序" prop="Sort">
          <el-input v-model.number="addForm.Sort"></el-input>
        </el-form-item> 
        <el-form-item label="页面状态" prop="Show" v-if="treeType == '页面'">
          <el-radio-group v-model="addForm.Show">
            <el-radio label="true">显示</el-radio>
            <el-radio label="false">隐藏</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetVerForm()">取 消</el-button>
        <el-button type="primary" @click="commitAddForm()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "limit",
  data() {
    return {
      treeType:'页面',
      tableData: [],
      pages: {},
      showEdit: false,
      showRolePage: false,
      addForm: {
        ID: 0,
        Name: "",
        Label: "",
        Path:"/",
        Icon:"",
        ParentID: 0,
        Type:'页面',
        Sort:0,
        Show: "true",
      },
      rules: {
        Name: [{ required: true, message: "请输入name", trigger: "change" }],
        Label: [{ required: true, message: "请输入名称", trigger: "change" }],
        Path: [{  message: "请输入路径", trigger: "change" }],
        Icon: [{  message: "请输入图标类名", trigger: "change" }]
      },
      condition: {
        name: "",
        pageNo: 1
      },
      pageData: [],
      defaultProps: {
        children: "Children",
        label: "Name"
      },
      defaultCheck: [],
      editId: "",
      currentType:0,//弹窗的状态是新增还是修改
      dataPid:1,//最外层的id
      typeAdd:false,//添加子级时路径必须添加
      sortArr:[],//排序处理数组
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    handleClick(tab) {
      this.treeType = tab.name;
      this.loadData();
    }, 
    loadData() {
      this.apiSever.get(`${this.URl.MENU+'?Type='+this.treeType}`, res => {
        this.pageData = res.data;
      });
    },
    allowDrop(dragNode, dropNode, type) {
      if (dragNode.data.type === 3 && type === "inner") return false;
      if (dragNode.data.type === 3 && dropNode.level > 1) return false;
      if (dropNode.data.type === 2 && type === "inner") return false;
      if (dropNode.data.type === 3 && type === "inner") return false;
      return true;
    },
    //打开新增页面 pty:1 新增  2修改  0最外层添加
    addPage(pty,data) {
      this.currentType = pty
      if(pty == 0){
        this.typeAdd = false
      }else if(pty == 1){
        this.typeAdd = true
        this.addForm.ParentID = data.ID
      }else if(pty == 2){
        if(data.Children&&data.Children.length>0){
          this.typeAdd = false
        }else{
          this.typeAdd = true
        }
        this.addForm = _.clone(data);
        // this.addForm.row = data;
      }
      this.showEdit = true;
    },
    //提交新增页面
    commitAddForm() {
      this.$refs["addForm"].validate(valid => {
        if (valid) {
          if(this.treeType == '页面'){
            this.addForm.Type = '页面'
          }else{
            this.addForm.Type = '接口'
          }
          if(this.currentType == 0){//创建第一级
            this.addForm.ID = 0
          }else if (this.currentType == 1) {//创建
            
          } else if(this.currentType == 2){//修改
            // delete this.addForm.row;
          }
          this.sureBtn(this.addForm)
        } else {
          return false;
        }
      });
    },
    // 确定修改&a=edit_node 删除  新增
    sureBtn(addForm){
      this.apiSever.post(this.URl.MENUEDIT,this.addForm, data => {
        this.$message({
          type: "success",
          message: "操作成功"
        });
        this.showEdit = false;
        this.resetVerForm()
        this.loadData();
      });
    },
    
    //关闭新增修改页面弹窗
    resetVerForm() {
      this.showEdit = false;
      this.$refs["addForm"].resetFields();
      this.addForm = {
        ID: 0,
        Show:'true',
      };
      
    },
    //保存修改的页面
    saveEdit() {
      api.post(URL.POWER_PAGE_EDIT, { page_tree: this.pageData }, data => {
        this.loadData();
      });
    },
    delPage(id) {
        let params ={
          ID :id
        }
        this.$confirm(`确定要删除吗?`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
            this.apiSever.post(this.URl.MENUDELETE,params, data => {
              this.loadData();
              this.$message({
                type: "success",
                message: "删除成功"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "取消操作"
            });
          });
    },
    // 输入排序数字
    sortChange(data){
      if(this.sortArr.length>0){
        this.sortArr.some((item,index)=>{
          if(item.id === data.id){
            this.sortArr[index].listorder = data.listorder
            return true;
          }else{
            this.sortArr.push({id:data.id,listorder:data.listorder})
            return true;
          }
        })
      }
      else if(this.sortArr.length == 0){
        this.sortArr.push({id:data.id,listorder:data.listorder})
      }
    },
    //排序提交
    sortSubmit(){
      let params ={
        param: this.sortArr
      }
      api.post(URL.SORT_ORDER,params, data => {
        this.loadData();
        this.sortArr = []
          this.$message({
            type: "success",
            message: "修改成功"
          });
      });
    }
  }
};
</script>
<style lang="less" scoped>
  .role {
    min-width: 730px;
  }
  .tabs{
    font-size: 18px;
    width: 700px;
  }
  .addPage-btn{
    margin-top: 20px;
  }
  .tree-content{
    padding: 20px 20px 50px;
    background-color: #fff;
  }
  .tree1{
    margin-top: 50px;
    width: 700px;
  }
  /deep/ .el-tree-node__content{
    height: 38px;
  }
  // .tree1 >>> .el-tree-node.is-drop-inner {
  //   color: #fff;
  // }
  .el-button--text{
    font-size: 14px;
  }
  .icon-titlle{
    width: 20px;
    margin-right: 5px;
    vertical-align: middle;
  }
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
    .sort_id {
      width: 30px;
    }
    // .sort_id >>> .el-input__inner{
    //     padding: 0 0px;
    //     text-align: center;
    //   }
  }
  

</style>
